<template>
  <div class="content">
    <el-form :model="signInform" ref="signInform" :rules="rules" label-position="right" label-width="80px">
      <el-form-item prop="name" label="" >
        <el-input v-model="signInform.name" clearable></el-input>
      </el-form-item>
      <el-form-item prop="password" label=""  autocomplete="off" >
        <el-input v-model="signInform.password" type="password" show-password></el-input>
      </el-form-item>
      <el-form-item prop="checkPass" label=""  autocomplete="off">
        <el-input v-model="signInform.checkPass" type="password" show-password></el-input>
      </el-form-item>
      <el-form-item prop="moveTel" label="">
        <el-input v-model="signInform.moveTel"></el-input>
      </el-form-item>
      <el-form-item prop="sex" label="" class="content-sex">
        <el-radio-group v-model="signInform.sex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="address" label="">
        <el-select v-model="signInform.address" placeholder="">
          <el-option key="0" label="" value=""></el-option>
          <el-option key="1" label="" value=""></el-option>
          <el-option key="2" label="" value=""></el-option>
          <el-option key="3" label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="menu" label="">
        <el-input v-model="signInform.menu" type="textarea" :autosize="{ minRows: 4, maxRows: 8}"></el-input>
      </el-form-item>
      <el-form-item prop="type" class="content-check">
        <el-checkbox label="1" name="type"  v-model="signInform.type"></el-checkbox>
        <!--<el-checkbox-group>
        </el-checkbox-group>-->
      </el-form-item>

    </el-form>
    <div class="content-button">
      <el-button type="primary" round @click="addPlan('signInform')">&nbsp;&nbsp;&nbsp;</el-button>
      <el-button round @click="resetForm('signInform')">&nbsp;&nbsp;&nbsp;</el-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "signIn",
        data: function() {
            let validatePass = (rule, value, callback) => {
                if (value === '') {
                    console.log(value)
                    callback(new Error(''));
                } else {
                    if (this.signInform.checkPass !== '') {
                        this.$refs.signInform.validateField('checkPass');
                    }
                    callback();
                }
            };
            let validatePass2 = (rule, value, callback) => {
                if (value == '') {
                    callback(new Error(''));
                } else if (value != this.signInform.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                signInform: {
                    name: '',
                    password: '',
                    checkPass: '',
                    // age: '',
                    sex: '',
                    // hobby: [],
                    address: '',
                    // email: '',
                    // sfz: '',
                    // tel: '',
                    moveTel: '',
                    // webId: '',
                    menu: '',
                    type: [],
                    formLabelWidth: '80px',
                },
                rules: {
                    name: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            min: 5,
                            max: 10,
                            message: '5~10',
                            trigger: 'blur'
                        },
                    ],
                    password: [
                        {
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        {
                            min: 6,
                            max: 30,
                            message: '6',
                            trigger: 'blur'
                        },
                        {
                            pattern: /^(\w){6,20}$/,
                            message: '6-20',
                            trigger: 'blur'
                        },
                        {
                            validator: validatePass,required: true, trigger: 'blur' ,
                        },
                    ],
                    checkPass: [{
                        required: true ,
                        message: '',
                    },
                        {
                            validator: validatePass2,trigger: 'blur',
                        },
                    ],
                    /*age: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            pattern: /^[1-9][0-9]{0,1}$/,
                            message: '1-120',
                            trigger: 'blur'
                        }
                    ],
                    hobby: [{
                        type: 'array',
                        required: true,
                        message: '',
                        trigger: 'change'
                    }],*/
                    address: [{
                        required: false,
                        message: '',
                        trigger: 'change'
                    }],
                    /*email: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            pattern: /^([a-zA-Z0-9]+[-_\.]?)+@[a-zA-Z0-9]+\.[a-z]+$/,
                            message: '',
                            trigger: 'blur'
                        }
                    ],
                    sfz: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
                            message: '',
                            trigger: 'blur'
                        }
                    ],
                    tel: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            pattern: /0\d{2}-\d{7,8}$/,
                            message: '+,010-1234567',
                            trigger: 'blur'
                        }
                    ],*/
                    moveTel: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            pattern: /^[1][3,4,5,7,8][0-9]{9}$/,
                            message: '',
                            trigger: 'blur'
                        }
                    ],
                    /*webId: [{
                        required: true,
                        message: 'IP',
                        trigger: 'blur'
                    },
                        {
                            pattern: /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/,
                            message: 'IP',
                            trigger: 'blur'
                        }
                    ],*/
                    menu: [{
                        required: false,
                        message: '',
                        trigger: 'blur'
                    },
                        {
                            min: 6,
                            max: 30,
                            message: '6',
                            trigger: 'blur'
                        },
                    ],
                    type: [{
                        // 'is': true,
                        type: 'array',
                        required: true,
                        message: '',
                        trigger: 'change'
                    },
                    ]
                },
            }

        },
        methods: {
            addPlan: function(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                        console.log(this.signInform)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                if (this.$refs[formName]!==undefined) {
                    this.$refs[formName].resetFields();
                }
            }
        },
    }
</script>

<style  lang="scss">
  .content{
    padding: 40px 40px 30px 40px;
    max-height:100%;
    .el-select{
      width: 100%;
    }
    .el-textarea__inner{
      resize: none;
    }

    .content-sex{
      .el-form-item__label{
        line-height: 30px;
      }
      .el-form-item__content{
        line-height: 30px;
      }
    }
    .content-check{
      .el-form-item__error{
        margin-left: 0;
      }
      .el-form-item__content{
        line-height: 20px;
        /*margin-left: 0 !important;*/
      }
    }
    .content-button{
      text-align: center;
    }
  }
</style>



