<template>
  <div id="layout">
    <div id="app-header">
      <div class="app-header-inner">
        <span @click="offEvent"> <router-link :to="{path : '/'}"><img class="header-logo" src="../assets/logo.png" alt=""></router-link>
       </span>
        <div class="header-nav">
          <ul class="nav-list" >
            <li v-show="userInfo.name" class="userName">{{userInfo.name}}</li>
            <li v-show="userInfo.name" @click="logOut" class="logOut"></li>
            <li v-show="!userInfo.name" @click="openLogInDialog"></li>
            <li v-show="!userInfo.name" @click="openSignInDialog"></li>
            <li @click="changePage"><router-link to="/about"></router-link></li>
          </ul>
        </div>
      </div>
    </div>
    <div id="app-content">
  <!--<div class="app-content-inner">-->
<!--        <keep-alive>-->
          <router-view></router-view>
<!--        </keep-alive>-->
  <!--</div>-->
    </div>
    <div id="app-footer">
      <p>
        <span>&copy; 2019</span>
        <a href="" target="_blank">ICP12345678</a>
        <a href="" target="_blank">31011311311211</a>
      </p>
    </div>
    <my-dialog :is-show-dialog="isShowLogInDialog" :dialog-width = "dialogWidth" @close-dialog = "closeDialog('isShowLogInDialog')">
      <log-in @log-in-submit="logInSubmit"></log-in>
    </my-dialog>
    <my-dialog :is-show-dialog="isShowSignInDialog" :dialog-width = "dialogWidth" @close-dialog = "closeDialog('isShowSignInDialog')">
      <sign-in></sign-in>
    </my-dialog>
    <!--<my-dialog :is-show-dialog="isShowAboutDialog" @close-dialog = "closeDialog('isShowAboutDialog')" ref="myDialog">
      <div class="scrollbarWrap">
        <div style="padding: 40px 40px 30px 40px; line-height: 1.8; font-size: 14px;text-indent: 2em;text-align: justify">
        <p>
          </p>
        <p>
        </p>
        <p>Impossible is nothing
        </p>
        <p>
        </p>
      </div>
      </div>
    </my-dialog>-->
  </div>
</template>

<script>
    import PropertyStore from '../assets/scripts/store.js'
  import MyDialog from './myDialog'
  import LogIn from "./logIn";
  import SignIn from "./signIn";
    export default {
        name: "layout",
        components : {SignIn, LogIn, MyDialog},
        data () {
            return {
                dialogWidth:"400px",
                isShowLogInDialog : false,
                isShowSignInDialog : false,
                isShowAboutDialog : false,
                userInfo : {
                    name:""
                },
                scrollbarEle: ""
            }
        },
        methods : {
            openLogInDialog () {
                this.isShowLogInDialog = true
            },
            openSignInDialog () {
                this.isShowSignInDialog = true
            },
            openAboutDialog () {
                this.isShowAboutDialog = true
                this.$refs.myDialog.parentHandleClick();
            },
            closeDialog (attr) {
                this[attr] = false
            },
            logInSubmit (data) {
                this.userInfo.name= data
                this.closeDialog('isShowLogInDialog')
                // console.log(data)
            },
            logOut(){
                this.userInfo.name=""
            },
            offEvent(){
                $('html, body').css({overflow: 'auto'})
                PropertyStore.state.property.currentId= 0;
                PropertyStore.state.property.prevId= -1;
                PropertyStore.state.property.eventAnable= false;
                this.setEvent(false)
                this.loadComplete(PropertyStore.e)
            },
            changePage(){
                this.$router.go(0);
            }
        },
        mounted() {
        }
    }
</script>

<style lang="scss">
  @import "../../static/style/param";
  @import "../../static/style/jquery.mCustomScrollbar.css";
  html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
  }

  /* HTML5 display-role reset for older browsers */

  article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
  }

  body {
    line-height: 1;
    color: #363636;
    font-size: $base_font_size;
    background-color: #f2f2f2;
    overflow-x: hidden;
  }

  ol, ul {
    list-style: none;
  }

  blockquote, q {
    quotes: none;
  }

  blockquote {
    &:before, &:after {
      content: '';
      content: none;
    }
  }
  q {
    &:before, &:after {
      content: '';
      content: none;
    }
  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  a{
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
  a:hover{
    color: #41b883;
  }
  /*layout*/
  #app-header{
    position: relative;
    background-color: #363636;
    color: #b2b2b2;
    height: 80px;
    line-height: 80px;
    width: 100%;
    z-index: 2000;
    /*margin-bottom: 30px;*/
    .app-header-inner{
      width: 80%;
      min-width: 1000px;
      height: 100%;
      margin: 0 auto;
      clear: both;
      overflow: hidden;
      a{
        .header-logo{
          padding-top: 18px;
          display: inline-block;
          height: 46px;
          width: auto;
        }
      }

      .header-nav{
        float: right;
        .nav-list{
          clear: both;
          overflow: hidden;
          li{
            &.userName{
              cursor: default;
              color: #ffffff;
            }
            position: relative;
            float: left;
            cursor: pointer;
            padding: 0 12px;
            transition: all 0.3s;
            &:last-child{
              /*padding-right: 0;*/
            }
            &:hover,
            &:hover a{
              color: #ffffff;
            }
            &:after{
              position: absolute;
              content: "";
              width: 1px;
              height: 14px;
              background-color: #b2b2b2;
              right: 0;
              top: 50%;
              margin-top: -6px;
              /*bottom: 0;*/
              /*margin: auto;*/
              overflow: hidden;
            }
            &:last-child:after{
              content: normal;
            }
            &.logOut:after{
              content: normal;
            }
          }
          /*.nav-pile{
              margin: 0 10px;
          }*/
        }

      }
    }
  }
  #app-footer{
    position: relative;
    width: 100%;
    padding: 18px 0;
    text-align: center;
    background-color: #e3e4e8;
    clear: both;
    /*margin-top: 30px;*/
    font-size: percentage(14/$base_font_size);
    z-index: 1000;
    p{
      min-width: 1000px;
    }
    a{
      display: inline-block;
      margin-left: 15px;
    }
  }
  #app-content{
    position: relative;
    width: 100%;
    /*background-color: #f1f1f1;*/
    .app-content-inner{
      width: 1200px;
      padding: 30px 0;
      margin: 0 auto;
    }
  }
  /**/
  .hot-icon{
    display: inline-block;
    font-size: percentage(12/$base_font_size);
    width: 36px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    border-radius: 100%;
    background-color: #a81a0b;
    color: #ffffff;
    margin-left: 5px;
  }
</style>



